<template>
  <div class="container">
    <span class="title">
      대외 활동
    </span>
<transition appear appear-active-class="animated slideInRight">
    <div class="content-header">
      <div class="content-title">
        {{ activity_detail.title }}
      </div>
      <!-- <div class="content-rate">
        <star-rating v-model="activity_detail.rate"></star-rating>
      </div> -->
      {{ activity_detail.rate }}
      <div class="d-day">
        <p>
          <span class="content-subtitle">{{ activity_detail.subtitle }}</span>
          <br>
          모집 마감까지 {{ activity_detail.d_day }}일 남음
        </p>
        <button class="applyment">
          지원하기
        </button>
        <div class="clearfix"></div>
      </div>
    </div>
    </transition>
      <div class="image-container">
<transition appear appear-active-class="animated slideInUp">
        <flickity id="flickity-container" ref="flickity" :options="flickity_options">
          <img class="carousel-cell content-image" v-for="img of activity_detail.image" :src="img.url" :key="img.id">
        </flickity>
</transition>
      </div>

<transition appear appear-active-class="animated slideInUp">
      <div class="article-container">
        <div class="article-title">
          활동 내용
        </div>
        <p class="article">
          {{ activity_detail.article }}
        </p>
      </div>
</transition>
  </div>
</template>

