            article: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec mi augue, molestie vitae turpis commodo, ullamcorper vehicula dolor. Phasellus nibh urna, suscipit vitae commodo at, mollis vitae augue. Curabitur ut leo sed orci interdum cursus lacinia et massa. In tristique, mauris eu convallis interdum, lacus odio varius ante, ac commodo libero sem vel mauris. Morbi bibendum faucibus pretium. Aliquam erat volutpat. Proin iaculis sem eget augue pretium, id lobortis diam tempor. Suspendisse neque felis, commodo ac tincidunt a, egestas non arcu. Nullam rhoncus commodo ante, vel convallis neque elementum a. Donec nulla ante, dictum ultrices justo vel, tempor vehicula orci. Vestibulum cursus sapien ipsum, eu semper sapien maximus vitae. Mauris quis hendrerit diam. Curabitur auctor purus sit amet mattis tincidunt. Etiam egestas, diam vitae pellentesque consectetur, nisl libero auctor est, ac consequat est ante volutpat neque.

Praesent sollicitudin porta nulla, at suscipit ex mollis non. Curabitur sed malesuada dolor. Sed eget arcu semper, iaculis dui et, dapibus purus. Phasellus mattis orci at nisl ornare, nec fringilla neque eleifend. Praesent vel tristique ligula. Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. Etiam condimentum accumsan eleifend. Suspendisse potenti. Aenean quis neque leo.

Proin gravida erat sapien. Pellentesque sodales id augue id aliquam.`,
            aplly_url: '',
          },
        
        flickity_options: {
          initialIndex: 2,
          prevNextButtons: false,
          pageDots: false,
          wrapAround: true
        }
      }
    },

    methods: {
      next() {
        this.$refs.flickity.next();
      },

      previous() {
        this.$refs.flickity.previous();
      }
    }
