<style lang="scss" scoped>
  $content-title-font-size: 1.9rem;
  $subtitle-color: rgb(182, 179, 179);
  $article-font-size: 1.3rem;
  $subtitle-font-size: 1.2rem;
  $screen-left-margin-size: 5%;
  $apply-button-height: 3rem;

  .container {
    width: 100%;
    padding-top: 1rem;
    -webkit-overflow-scrolling: touch;
  }

  .title {
    font-size: 1rem;
    color: $subtitle-color;
    font-weight: 400;
    margin-left: $screen-left-margin-size;
  }

  .content-header {
    margin-left: $screen-left-margin-size;
  }

  .content-title {
    font-size: $content-title-font-size;
    font-weight: 890;
  }

  .content-subtitle {
    color: $subtitle-color;
    font-size: $subtitle-font-size;
    font-weight: 400;
  }

  .d-day {
    height: $apply-button-height;
    margin-bottom: -1.3rem;
    position: relative;
    font-weight: 500;
    font-size: 1.1rem;
    display: table;
    width: 100%;
    margin-top: 0.7rem;
  }

  .d-day p {
    display: table-cell;
    vertical-align: bottom;
  }

  .applyment {
    width: 9rem;
    height: $apply-button-height;
    border: 2px solid #fff;
    background: #fff;
    box-sizing: border-box;
    border-radius: 2px;
    float: right;
    margin-right: 1.5rem;
    box-shadow: 0 0 7px rgba(0,0,0,0.16);
    font-weight: 500;
  }

  .clearfix {
    content: "";
    display: block;
    clear: both;
  }

  .carousel-cell {
    width: 35rem;
    height: 300px;
    margin-left: 20px;
    border-radius: 8px;
  }

  #flickity-container {
    margin-top: 40px;
    border-radius: 1rem;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
  }

  img {
    object-fit: cover;
  }

  .article-container {
    margin-top: 2rem;
    text-align: justify;
    list-style: none; 
    padding-left: 7px;
    padding-right: 7px;
    border: 30px solid transparent;
    line-height: 1.3;
    border-image: url('../assets/card_background.png');
    border-image-slice: 50 50 120 50 fill;
    box-sizing: border-box;
    margin-bottom: 1rem;
  }

  .article-title {
    font-size: 1.2rem;
  }

  .article {
    font-size: 1rem;
  }
</style>


