<template>
  <div class="main-page">
    <transition appear appear-active-class="animated slideInUp">
      <image-card-view/>
    </transition>

    <transition appear appear-active-class="animated slideInUp">
      <notice-list-view/>
    </transition>

    <transition appear appear-active-class="animated slideInUp">
      <acitivity-list-view/>
    </transition>
  </div>
</template>

<script>
  import NoticeListView from '@/components/news-fragment/NoticeListView'
  import AcitivityListView from '@/components/news-fragment/ActivityListView'
  import ImageCardView from '@/components/news-fragment/ImageCardView'

  export default {
    name: 'NewsFragment',
    components: { 
      ImageCardView,
      NoticeListView,
      AcitivityListView 
    },
    data() {
      return {
      };
    }
  };
</script>

<style scoped>
  .main-page {
    -webkit-overflow-scrolling: touch;
    margin: 0 auto;
  }
</style>




