<template>

    <div class="image-card-view">
      <div class="image-card-view-content">
          <img :src="card.image">
          <div class="layer"></div>
        <p>
          <span class="content-title">{{ card.title }}</span>
          <br>
          <span class="content-subtitle">{{ card.subtitle }}</span>
        </p>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'ImageCardViewComponent',
    data() {
      return {
        card: {
          id: 1,
          title: '대만 교육센터',
          subtitle: '중국어 교육 서포터즈 1기 모집',
          image: 'http://drive.google.com/uc?export=view&id=17398Ly0IS9_Kx7cGuXtA-z8KWERxvDxv'
        },
      };
    }
  };
</script>

<style lang="scss" scoped>
$component-width: 86%;
$border-radius: 15px;

/* Make as card */
.image-card-view {
  // width: 90%;
  position: relative;

  // border: 2px solid rgba(187,198,206, 0.4);
  border-radius: $border-radius;
  box-shadow: 0 10px 8px -4px rgba(192,197,208, 0.8);
  line-height: 1.5;
  box-sizing: border-box;
  overflow: hidden;
  margin: 1rem 2rem 0 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  
}

.image-card-view-content {
  overflow: hidden;
}

img {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: $border-radius;
  z-index: 0;
  filter: blur(3.5px);
}

.layer {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.3;
  border-radius: $border-radius;
  overflow: hidden !important;
  z-index: 2;
}

p {
  position: relative;
  padding-top: 90px;
  padding-left: 20px;
  line-height: 1.2;
  z-index: 10;
}

.content-title {
  font-size: 1.5rem;
  color: #fff;
  font-weight: 500;
}

.content-subtitle {
  font-size: 1.5rem;
  color: #fff;
  font-weight: 500;
}

.detail-button {
  box-shadow: 0 4px 8px 2px rgba(0,0,0,0.15);
  border-radius: 8px;
  background: linear-gradient( to right, rgb(160, 160, 160), #777, #333, #111 );
  height: 66px;
  /* border: 30px solid transparent;
  border-image: url('../assets/card_background.png');
  border-image-slice: 50 50 120 50 fill;
  box-sizing: border-box; */
}

.detail-button>span {
  font-size: 1.5rem;
  color: #fff;
  display: block;
  text-align: center;
  padding-top: 20px;
}
</style>


