<template>
    <div class="activity-list-view">
      <div class="activity-list-view-title">
        {{ title }}
      </div>

      <div class="activity-content-wrapper">
        <transition-group appear appear-active-class="animated slideInUp">
        <li class="activity-content" @click="getDetails()" v-for="content of activity" :key="content.id">
            <img class="content-image" :src="content.image">
          <div class="activity-string-content">
            <span class="activity-content-title">{{ content.title }}</span>
            <br>
            <span class="activity-content-subtitle">{{ content.subtitle }}</span>
          </div>
            <div class="activity-content-footer">
              <span>모집 마감까지 {{ content.d_day }}일 남음</span>
            </div>
            <div class="clearfix"></div>
        </li>
        </transition-group>
      </div>

      <div class="activity-footer">
        <div class="activity-detail-button">
          <p>53개의 대외 활동 더 보기</p>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'ActivityListViewComponent',
    data() {
      return {
        title: '나에게 어울리는 대외활동' ,
        activity: [
          {
            id: 1,
            title: '[G마켓&옥션]글로벌 SNS 서포터즈',
            subtitle: '미래 클라우드',
            image: 'http://drive.google.com/uc?export=view&id=1OIMIcRUtEcPiAp847KnuH663m89Dd67H',
            d_day: 39
          },
          {
            id: 2,
            title: '[G마켓&옥션]글로벌 SNS 서포터즈',
            subtitle: '미래 클라우드',
            image: 'http://drive.google.com/uc?export=view&id=1OIMIcRUtEcPiAp847KnuH663m89Dd67H',
            d_day: 39
          },
        ],
      };
    },

    methods: {
      getDetails() {
        this.$router.push({ name: 'detail-page'});
      }
    }
  };
  
</script>

<style scoped>

/* Make as card */
.activity-list-view {
  border: 3rem solid transparent;
  border-image: url('../../assets/card_background.png');
  border-image-slice: 50 50 120 50 fill;
  /* background-color: aquamarine; */
  /* margin: 0 auto; */
}

.activity-list-view-title {
  text-align: left;
  font-weight: 890;
  font-size: 1.4rem;
  padding: 0px 0px 0px 5px;
  margin-bottom: 1.43rem;
}

.activity-content {
  list-style: none; 
  padding-top: 0.7rem;
  padding-left: 0.7rem;
  margin-top: 1rem;
  border: 2px solid rgba(187,198,206, 0.4);
  border-radius: 8px;
  box-shadow: 0 8px 6px -6px rgba(192,197,208, 0.5);
  line-height: 1.5;
  background: #fff;
  box-sizing: border-box;
}

.content-image {
  width: 7rem;
  height: 8rem;
  float: left;
  margin-right: 0.7rem;
  margin-bottom: 0.7rem;
}

.activity-string-content {
  font-weight: 500;
  padding-right: 2.5rem;
}

.activity-content-title {
  text-align: left;
  font-weight: 700;
  font-size: 1.3rem;
}

.activity-content-subtitle {
  font-size: 1.1rem;
  color: rgb(182, 179, 179);
  font-weight: 400;
}

.activity-content-footer {
  font-size: 1rem;
  display: inline-block;
  margin-top: 10px;
}

.activity-footer {
  margin-top: 1.43rem;
}

.clearfix {
  content: "";
  display: block;
  clear: both;
}

.activity-detail-button {
  display: table;
  width: 100%;
  box-shadow: 0 4px 8px 2px rgba(0,0,0,0.15);
  border-radius: 8px;
  background: linear-gradient( to right, rgb(160, 160, 160), #777, #333, #111 );
  height: 3.6rem;
  margin-bottom: 0.5rem;
}

.activity-detail-button>p {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  font-size: 1.1rem;
  color: #fff;
}

</style>



