<template>
    <div class="notice-list-view">
      <div class="notice-list-view-title">
        {{ title }}
      </div>

      <div class="content-wrapper">
        <transition-group appear appear-active-class="animated slideInUp">
        <li class="content" v-for="content of notice" :key="content.id">
            <span class="content-title">{{ content.title }}</span>
            <br>
            <span class="content-subtitle">{{ content.subtitle }}</span>
        </li>
        </transition-group>
      </div>

      <div class="footer">
        <div class="detail-button">
          <p>12개의 소식 더 보기</p>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'NoticeListViewComponent',
    data() {
      return {
        title: '나에게 필요한 정보' ,
        notice: [{
            id: 1,
            title: '정보통신 대학 사물함 철거 / 신청 공지',
            subtitle: '정보통신대학 학생회 페이스북 페이지 | 2017년 9월 3일',
          },
          {
            id: 2,
            title: '정보통신 대학 사물함 철거 / 신청 공지',
            subtitle: '정보통신대학 학생회 페이스북 페이지 | 2017년 9월 3일',
          },
          {
            id: 3,
            title: '정보통신 대학 사물함 철거 / 신청 공지',
            subtitle: '정보통신대학 학생회 페이스북 페이지 | 2017년 9월 3일',
          }
        ],
      };
    }
  };
  
</script>

<style lang="scss" scoped>
$content-padding-top: 1rem;
$content-padding-left: 1.3rem;
$contetn-margin-top: 1rem;


/* Make as card */
.notice-list-view {
  border: 3rem solid transparent;
  border-image: url('../../assets/card_background.png');
  border-image-slice: 50 50 120 50 fill;
  // margin: 0 auto;
}

.notice-list-view-title {
  text-align: left;
  font-weight: 890;
  font-size: 1.4rem;
  padding: 0px 0px 0px 5px;
  margin-bottom: 1.43rem;
}

.content {
  list-style: none; 
  padding: 0.7rem 1rem 0.7rem 1rem;
  margin-top: 1rem;
  border: 2px solid rgba(187,198,206, 0.4);
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 8px 6px -6px rgba(192,197,208, 0.5);
  line-height: 1.5;
  box-sizing: border-box;
  // height: 5rem;
}

.content-title {
  font-size: 1.1rem;
  font-weight: 700;
  color: rgb(26, 26, 26);
}

.content-subtitle {
  font-size: 0.9rem;
  /* font-size: 18px; */
  color: rgb(182, 179, 179);
  font-weight: 400;
}

.footer {
  margin-top: 1.43rem;
}

.detail-button {
  display: table;
  width: 100%;
  box-shadow: 0 4px 8px 2px rgbaa(0,0,0,0.15);
  border-radius: 8px;
  background: linear-gradient( to right, rgb(160, 160, 160), #777, #333, #111 );
  height: 3.6rem;
  margin-bottom: 0.5rem;
}

.detail-button>p {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  font-size: 1.1rem;
  color: #fff;
  // color: rgb(41, 152, 250);
}

</style>



